// @ts-nocheck
/* 控制台输出编译相关信息 */
function buildInfo() {
  const env = process.env.NODE_ENV;

  if (false || env !== 'development') {
    const logArr = [
      'project   windvane',
      `version   1.00.26`,
      `build in  2022-07-04 21:52:45`
    ];
    const maxLen = Math.max(...logArr.map((item, index) => item.length));
    const log = logArr.map((item, index) => `  ${ item.padEnd(maxLen)}  `).join('\n');
    const style = 'background-color: #faad14; color: #fff;';

    console.info(`%c${ log }`, style);
  }
}

buildInfo();