import { fromJS, Map } from 'immutable';
import { createAction, ActionFunction1 as AF1, Action } from 'redux-actions';
import { Reducer } from 'redux';
import { Effect } from 'dva';
import formatReducers, { formatAction } from '../utils/formatReducers';

// 登陆的返回值
type ResLoginData = {
  mail: string;
  username: string;
  username_zh_cn: string;
};

export const reqLogin: AF1<ResLoginData, Action<ResLoginData>> = createAction<any>('system/登陆请求');
export const setUsers: AF1<ResLoginData, Action<ResLoginData>> = createAction<any>('system/用户名');

export default {
  namespace: 'system',

  state: fromJS({
    users: undefined
  }),

  effects: formatReducers<Effect>({
  }),

  reducers: formatReducers<Reducer>({
    [setUsers.toString()]($$state: Map<string, any>, action: Action<ResLoginData>): Map<string, any> {
      return $$state.set('users', Map(action.payload));
    }
  })
};