import { transform } from 'lodash-es';
import { Dictionary } from 'lodash';
import { AnyAction } from 'redux';

/**
 * 格式化actions
 * @param { object } action
 */
export function formatAction(action: AnyAction): AnyAction {
  const type: string[] = action.type.split('/');

  if (type.length > 1) {
    type.splice(0, 1);
  }

  action.type = type;

  return action;
}

/**
 * 格式化reducers，去除key值里面的namespace
 * @param { object } reducers
 */
function formatReducers<T>(reducers: Dictionary<T>): Dictionary<T> {
  return transform(reducers, function(result: Dictionary<T>, value: T, key: string): void {
    const keyArr: string[] = key.split('/');

    if (keyArr.length > 1) {
      keyArr.splice(0, 1);
    }

    result[keyArr.join('/')] = value;
  }, {});
}

export default formatReducers;