import { message } from 'antd';

interface Dva {
  config: {
    onError: Function;
  };
}

export const dva: Dva = {
  config: {
    onError(err: any): void {
      message.error(err.message, 3);
      err.preventDefault();
    }
  }
};

export function getInitialState(): { [key: string]: any } {
  return {};
}